import React from 'react';
import ProfileInfo from './ProfileInfo/ProfileInfo';
import MyPostsContainer from './MyPosts/MyPostsContainer';

const Profile = (props) => {

  return (
    <div>
      <ProfileInfo profileInfo={props.profileInfo} profileStatus={props.profileStatus} updateProfileStatus={props.updateProfileStatus}/>
      <MyPostsContainer/>
    </div>
  );
}

export default Profile;
